#note-page {
  // background-image: url("/images/notes_background.jpg");
  .container {
  	margin-top: 80px;
  	padding-bottom: 15px;
  }
}
#notes {
  .title {
  	width: 300px;
  	display: inline-block;
  	overflow: hidden;
  	white-space: nowrap;
		text-overflow: ellipsis;
		text-overflow: " …";
		color: $white;
		vertical-align: middle;
		.text {
			vertical-align: middle;
		}
	}
	.list-header {
		border-left: 1px solid $lightBlue;
		margin-bottom: 2px;
		.title {
			overflow: hidden;
			white-space: nowrap;
			text-overflow: ellipsis;
			text-overflow: " …";
			width: 200px;
			display: inline-block;
		}
		h4 {
			color: $darkWhite;
			display: inline-block;
			vertical-align: middle;
		}
		&:hover {
			border-color: $limeA700;
			background-color: rgba(0,0,0,0.8);
		}
	}
}

@media(min-width: 970px){
	#note-page {
		.cke_contents {
			height: 430px !important; 
		}
	}
	#notes {
		.accordion-body {
			max-height: 360px;
			overflow-y: scroll;
		}
	}
}
@media (min-width: 1500px){
	#note-page, .footer-fixed-bottom  {
		.container {
		  width: 1400px;
		}
		.cke_contents {
			height: 600px !important;
		}
	}
	#notes {
		.accordion-body {
			max-height: 400px;
			overflow-y: scroll;
		}
	}
}
@media (min-width: 1900px){
	#note-page {
		.cke_contents {
			height: 760px !important;
		}
	}
	#notes {
		.accordion-body {
			max-height: 400px;
			overflow-y: scroll;
		}
	}
}
@media (min-width: 1800px){
	#note-page, .footer-fixed-bottom {
		.container {
		  width: 1780px;
		}
	}
	#notes {
		.accordion-body {
			max-height: 550px;
			overflow-y: scroll;
		}
	}
}
